<template>
  <div class="order" v-if="order">
    <div class="header">
      <div class="picTxt">
        <div class="text">
          <p>订单信息</p>
          <p>
            累计订单:&nbsp;{{ order.order_count }}总消费:&nbsp;￥{{
              order.sum_price
            }}
          </p>
        </div>
        <div class="pictrue">
          <img src="../assets/order.png" alt="" />
        </div>
      </div>
    </div>
    <div class="orderState1">
      <van-tabs @click="onClick" v-model="active">
        <van-tab v-for="(item, index) in orders" :key="index">
          <template #title>
            <p>{{ item.text }}</p>
            <p>{{ item.num }}</p>
          </template>
          <div
            class="xinxi"
            v-for="limitest in limit"
            :key="limitest.id"
          >
            <div>
              <span>{{ limitest.add_time | formatDate("Y-m-d h:M:S") }}</span
              ><span>{{ limitest._status._title }}</span>
            </div>
            <div class="commodity" v-for="cartInfo in limitest.cartInfo" :key="cartInfo.id">
              <router-link :to="`/detail/${cartInfo.productInfo.id}`" class="commodityson1" tag="div">
                <div class="pictrue">
                  <img :src="cartInfo.productInfo.image" alt="" />
                </div>
                <div class="text">
                  <div class="name">
                    {{ cartInfo.productInfo.store_name }}
                  </div>
                  <div class="money">
                    <div>￥{{ cartInfo.productInfo.price }}</div>
                    <div>x{{ cartInfo.cart_num }}</div>
                  </div>
                </div>
              </router-link>
            </div>
            <div class="totalPrice">
              共{{ limitest.total_num}}件商品,总金额
              <span
                >￥{{limitest.total_price}}
              </span>
            </div>
            <div class="bottom" v-if="item.text=='待发货'">
                <van-button round color="#e93323">查看详情</van-button>
            </div>
            <div class="bottom" v-if="item.text=='待付款'">
                <van-button round  class="quxiao">取消订单</van-button>
                <van-button round color="#e93323">立即付款</van-button>
            </div>
          </div>
          <div class="pictrue" v-show="limit.length == 0">
            <img src="../assets/order1.png" alt="" />
          </div>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      order: null,
      orders: [],
      active: 1,
      limit: [],
    };
  },
  created() {
    this.$axios.get("/api/order/data").then((d) => {
        this.order = d.data.data;
        console.log(d.data.data);
        this.orders = [
          { text: "待付款", num: d.data.data.unpaid_count },
          { text: "待发货", num: d.data.data.unshipped_count },
          { text: "待收货", num: d.data.data.received_count },
          { text: "待评价", num: d.data.data.evaluated_count },
          { text: "已完成", num: d.data.data.complete_count },
        ];
      });
      this.$axios
        .get("/api/order/list?page=1&limit=20&type=" + this.$route.params.type)
        .then((d) => {
          this.active = this.$route.params.type;
          this.limit = d.data.data;
        });
  },
  
  methods: {
    onClick(name) {
      this.$route.params.type = name;
      this.$axios
        .get("/api/order/list?page=1&limit=20&type=" + this.$route.params.type)
        .then((d) => {
          this.active = this.$route.params.type;
          this.limit = d.data.data;
          console.log(this.limit);
        });
    },
    Active(event) {
      console.log(event);
    },
  },
  filters: {
    formatDate(value, fstr) {
      let d = new Date(value * 1000);
      let formatO = {
        "Y+": d.getFullYear(),
        "m+": d.getMonth() + 1,
        "d+": d.getDate(),
        "h+": d.getHours(),
        "M+": d.getMinutes(),
        "S+": d.getSeconds(),
      };
      for (let k in formatO) {
        let reg = new RegExp(k);
        let val = formatO[k];
        val = val < 10 ? "0" + val : val;
        fstr = fstr.replace(reg, val);
      }
      return fstr;
    },
  },
};
</script>
<style lang="less">
.order {
  .header {
    height: 133px;
    padding: 0 15px;
    background-color: #e93323;
    .picTxt {
      display: flex;
      align-items: center;
      justify-content: space-between;
      height: 97px;
      .text {
        width: 200px;
        height: 51.23px;
        p {
          color: hsla(0,0%,100%,.8);
          margin: 0;
          font-size: 13px;
          &:nth-child(1) {
            font-size: 17.5px;
            font-weight: 700;
            margin-bottom: 10.24px;
          }
        }
      }
      .pictrue {
        width: 62.5px;
        height: 55.8px;
        img {
          width: 62.5px;
          height: 55.8px;
        }
      }
    }
  }
  .orderState1 {
    margin: 0 15px;
    margin-top: -40px;
    border-radius: 3px;
    color: #282828;
    .pictrue {
      display: flex;
      align-items: center;
      justify-content: center;
      img {
        margin: 35px 0 25px 0;
        width: 200px;
      }
    }
    .van-grid-item__content {
      padding: 0;
    }
    .van-tabs__wrap {
      margin-bottom: 10px;
      height: 70px;
      border-radius: 3px;
      .van-tab {
        padding: 5px 0 10px 0;
        box-sizing: initial;
        height: 45.5px;
        .van-tab__text--ellipsis {
          display: -webkit-box;
          overflow: hidden;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          p {
            margin: 5px 0 0 0;
            font-size: 13px;
            color: #454545;
            text-align: center;
          }
        }
      }
    }
    .van-tabs__content{
      .xinxi {
        background-color: white;
        margin-top: 5px;
        padding-top: 10px;
        div {
          padding: 0 15px;
          &:first-child {
            font-size: 14px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            span {
              &:nth-child(2) {
                color: #fc4141;
              }
            }
          }
        }
        .commodity {
          height: 60px;
          height: 60px;
          .commodityson1 {
            margin-top: 11px;
            padding: 0;
            .pictrue {
              width: 60px;
              height: 60px;
              padding: 0;
              img {
                width: 60px;
                height: 60px;
                margin: 0;
              }
            }
            .text {
              width: 243px;
              height: 38px;
              display: flex;
              justify-content: space-between;
              font-size: 14px;
              padding: 0;
              .name {
                padding: 0;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
                overflow: hidden;
                height: 38px;
                width: 153px;
              }
              .money {
                padding: 0;
                height: 38px;
                width: 57px;
                color: #999;
                div {
                  padding: 0;
                  &:nth-child(2) {
                    display: flex;
                    justify-content: flex-end;
                  }
                }
              }
            }
          }
        }
        .totalPrice {
          height: 19px;
          margin: 13.5px 0 0 0;
          border-bottom: .01rem solid #eee;
          padding-bottom: 10px;
          font-size: 13px;
          text-align: end;
          span {
            color: #fc4141;
            font-weight: 700;
          }
        }
        .bottom{
          height: 53.5px;
          padding: 0 15px;
          display: flex;
          align-items: center;
          justify-content: flex-end;
          .van-button--normal{
            padding: 0;
          }
          .van-button{
            height: 30px;
          }
          .quxiao{
            margin-right: 8px;
          }
        }
      }
    }
  }
}
</style>